.service{
	background-color: var(--primaryColor);
	padding-top: 7rem;
	perspective: 1000px;
	padding-bottom: 5rem;
	margin-top: 5rem;
	color:var(--primaryFontColor);
	h2{
		margin-bottom: 1rem;
		font-size: 2.4rem;
		font-weight: 900;
		text-align: center;
	}
	.serviceDesc{
		margin-bottom: 7rem;
		line-height: 2;
		font-size: 1rem;
		text-align: center;
	}
	@media(max-width:768px){
		margin-top: 0rem;
	}
	@media(max-width:640px){
		padding-bottom: 0rem;
		margin-top: 0rem;
		h2{
			font-size: 1.6rem;
		}
		.serviceDesc{
			margin-bottom: 4rem;
		}
	}
	 .item {
		position: relative;
		&:hover{
			.hidden-content {
				top: -100px;
				opacity: 1;
				visibility: visible;
				@media(max-width:768px){
					z-index: 3;
				}
		 	 }
			.showed-content {
				top: 90px;
				@media(max-width:768px){
					top:35px;
				}
			}
		 }
		 @media(max-width:768px){
			 margin-bottom: 3rem;
		 }
	  }
	  
	  
	  .hidden-content {
		background: rgb(255,77,30);
		background: linear-gradient(105deg, rgba(255,77,30,1) 0%, rgba(255,44,109,1) 100%);
		padding: 30px;
		border-radius: 20px;
		text-align: center;
		opacity: 0;
		top: 0;
		visibility: hidden;
		position: absolute;
		z-index: 2;
		transition: all 0.5s;
		&::after {
			width: 20px;
			height: 20px;
			position: absolute;
			background: rgb(255,77,30);
			content: '';
			left: 50%;
			bottom: -8px;
			margin-left: -5px;
			transform: rotate(45deg);
			background: linear-gradient(105deg, rgba(255,52,69,1) 0%, rgba(255,51,78,1) 100%);
			z-index: -1;
		  }
		  h4 {
			font-size: 20px;
			font-weight: 700;
			color: #fff;
			margin-bottom: 20px;
		  }
		  p {
			color: #fff;
		  }
	  }
	
	  .showed-content {
		top: 0px;
		position: relative;
		z-index: 3;
		background-color: #fff;
		text-align: center;
		padding: 50px;
		border-radius: 20px;
		box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
		transition: all 0.5s;
		img {
			max-width: 100px;
		  }
		 
	  }

}
