.heroSection{
	position: relative;
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	position: relative;
	height: 100vh;
	.container{
		height: 100%;
		.row{
			height: 100%;
			display: flex;
			align-items: center;
		}
	}
	.mainHeading{
		line-height: 1.5;
		font-weight: 900;
		font-size: 3.5rem;
		letter-spacing: 1px;
	}
	p{
		margin-top: 2.4rem;
		font-size: 1.2rem;
		line-height: 2;
	}
	button{
		margin-top: 2rem;
	}
	z-index: 1;
	@media(max-width:768px){
		.mainHeading{
			font-size: 2.5rem;
		}
	}
	@media(max-width:480px){
		.mainHeading{
			font-size: 2rem;
		}
		p{
			font-size: 1rem;
		}
	}
}
.heroSection::after{
		z-index: -1;
		background-image: url("../../images/bg-2.jpg");
		background-repeat: no-repeat;
		background-size: cover;
		position: absolute;
		background-attachment: fixed;
		left: 0;
		top: 0px;
		width: 100%;
		content:"";
		height: 100%;
}