.product{
	padding-top: 8rem;
	h2{
		margin-bottom: 1rem;
		font-size: 2.4rem;
		font-weight: 900;
		text-align: center;
	}
	.serviceDesc{
		margin-bottom: 7rem;
		line-height: 2;
		font-size: 1rem;
		text-align: center;
	}
	@media(max-width:768px){
		padding-top: 2rem;
	}
	@media(max-width:640px){
		h2{
			font-size: 1.6rem;
		}
		.serviceDesc{
			margin-bottom: 4rem;
		}
	}
	.item{
		.thumb{
			margin:1rem;
			height: 100%;
			position: relative;
			img{
				width: 100%;
				display: block;
			}
			.hover-effect{
				position: absolute;
				top:0;
				left:0;
				width: 100%;
				height: 100%;
				display: none;
				.inner-content{
					padding:2rem;
					display: flex;
					justify-content: center;
					flex-direction: column;
					height: 100%;
					h2{
						font-size: 2.1rem;
						font-weight: 800;
					}
					p{
						font-size: 1rem;
						line-height: 1.6;
						margin-top: 2rem;
					}
				}
			}
			&:hover{
				.hover-effect{
					background-color: var(--secondaryColor);
					display: block;
					opacity: 0.9;
				}
			}
		}
	}
}