.team{
	padding-top:10rem;
	padding-bottom: 5rem;
	.teamDesc{
		h2{
			margin-bottom: 1rem;
			font-size: 2.5rem;
			font-weight: 900;
			text-align: center;
		}
		p{
			margin-bottom: 8rem;
			line-height: 2;
			font-size: 1rem;
			text-align: center;
		}
	}
	@media(max-width:640px){
		padding-top: 5rem;
		.teamDesc{
			h2{
				font-size: 1.6rem;
			}
			p{
				margin-bottom: 4rem;
			}
		}
	}

	.media {
		background: #ffffff;
		-webkit-box-shadow: 0px 2px 8px 0px rgba(50, 50, 50, 0.08);
				box-shadow: 0px 2px 8px 0px rgba(50, 50, 50, 0.08);
		position: relative;
		bottom: 40px;
		display: flex;
		margin-bottom: 50px;
		@media(max-width:640px){
			display: block;
			.media-object{
				img{
					width: 100%;
					display: block;
				}
			}
		}
	  }
	  .media-body {
		   padding: 40px 20px 35px 20px; 
		   h3{
			   margin-bottom: 0.2rem;
		   }
		   h4{
			   margin-bottom: 1rem;
			   color:var(--themeMainColor);
		   }
		   p{
				line-height: 1.6;
		   }
	 }
	  .border-r { border-right: 4px solid var(--themeMainColor); }
	 
	  

}