.aboutUs{
	background-color: var(--primaryColor);
	color:var(--primaryFontColor);
	padding-bottom: 13rem;
	padding-top: 13rem;
	position: relative;
	.descAbout{
		margin-top: 1rem;
	}
	figure{
		img{
			width: 100%;
			overflow: hidden;
		}
	}
	@media(max-width:768px){
		padding-top: 5rem;
		padding-bottom: 5rem;
	}
	.aboutDesc{
		position: relative;
		height: 100%;
		flex-direction: column;
		background-color: var(--primaryColor);
		display: flex;
	
		justify-content: center;
		height: 100%;
		padding:4rem;
		h2{
			font-weight: 900;
			font-size: 2.5rem;
		}
		p{
			line-height: 1.7;
			font-size: 16px;
			margin-top: 2rem;
			
		}
		button{
			margin-top: 2rem;
			width: 150px;
		}
	}
	
}

.aboutUs::after{
	background-image: url("../../images/about-right.png");
	background-repeat: no-repeat;
	background-size: cover;
	position: absolute;
	right: 0;
	top: 0px;
	height: 100%;
	width: 400px;
	content:"";
}
