.contact{

	padding-top: 10rem;
	padding-bottom: 10rem;
	color:#fff;
	background-image: url("../../images/businessman-touching-virtual-screen.jpg");
	background-position: center;
	background-size: cover;
	position: relative;
	background-attachment: fixed;
	@media(max-width:768px){
		padding-bottom: 3rem;
	}
	.contactAbsolute{
		width: 100%;
		height: 100%;
		position: absolute;
		top:0;
		left:0;
		background-color: rgba(0,0,0,0.8);
	}
	.footer-item{
		.logo{
			font-weight: bold;
			font-size: 2rem;
		}
		h4{
			font-size: 1.3rem;
		}
		p{
			line-height: 1.6;
			margin-top: 2rem;
		}
		.about-desc{
			margin-top: 2rem;
		}
		ul{
			margin-top: 1.4rem;
			li{
				padding-top: 1rem;
				padding-bottom: 1rem;
				a{
					color:#f0f0f0;
				}
			}
		}
		form {
			background-color: #fff;
			height: 3rem;
			border-radius: 1.5rem;
			position: relative;
			margin-top: 2rem;
			input {
				line-height: 46px;
				background-color: transparent;
				border: none;
				font-size: 0.8rem;
				padding: 0px 1.4rem;
				outline: none;
				width: 90%;
				::placeholder {
					color: #000;
				  }
			  }
			button {
				position: absolute;
				right: 1.2rem;
				top: 50%;
				transform: translateY(-50%);
				background-color: transparent;
				border: none;
				outline: none;
				svg{
					color: var(--themeMainColor);

				}
			  }
		  }
		  @media(max-width:768px){
			  margin-bottom: 5rem;
		  }
	}
	
}