.headerWrapper{
	color:#000;
	transition: 0.3s;
	// background-color: #fff;
	position: fixed;
	width: 100%;
	padding:1.5rem 0rem;
	top:0;
	z-index: 100;
	@media(max-width:768px){
		padding:1.5rem;
	}
	@media(max-width:640px){
		padding:1rem;
	}
	.logo{
		font-weight: 900;
		font-size: 1.5rem;
		height: 100%;
		display: flex;
		align-items: center;
	}
	&.stickyHeader{
		position: fixed;
		width: 100%;
		box-shadow: 0px 0px 4px #e2e2e2;
		background-color: #fff;
		animation:moveBottom 0.3s ease;
	}
	.menu{
		display: flex;
		justify-content: flex-end;
		.deskMenu{
			display: flex;
			@media(max-width:1024px){
				display: none;
			}
			li{
				list-style-type: none;
				cursor: pointer;
				transition: 0.3s all;
				a{
					padding:0.5rem 1rem;
					display: block;
					color:#000;
				}
				&:hover{
				
					border-radius: 0.3rem;
					a{
						background-color: var(--themeMainColor);
						color:var(--themeFontColor);
					}
				}
			}
		}
		.mobMenu{
			display: none;
			@media(max-width:1024px){
				display: block
			}
			.iconLinkBtn{
				background-color: transparent;
				border: none;
				svg{
					color:#000;
				}
			}
		}
		.mobMenuList{
			position: fixed;
			width: 100%;
			left:0;
			top:5rem;
			background-color: var(--primaryColor);
			color:var(--primaryFontColor);
			z-index:1;
			li{
				list-style-type: none;
				padding:1rem 1.5rem;
				a{
				color:#000;
				}
				&:hover{
					background-color: var(--themeMainColor);
					a{
					color:var(--themeFontColor);
						
					}
				}
			}
			
		}
	}
}

@keyframes moveBottom{
	0%{
		transform: translateY(-80px);
	}
	100%{
		transform: translateY(0px);

	}
}